import { Fragment, useRef, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { Dialog, Transition } from '@headlessui/react'

export default function Alert({ duId }) {
    const [open, setOpen] = useState(true)
    const navigate = useNavigate();
    const [isRedirected, setIsRedirected] = useState(false);
    const cancelButtonRef = useRef(null)


    const redirectToContentDetail = () => {
        setIsRedirected(true);
        navigate('/contentdetail/' + duId)
        // navigate("/contentdetail")
    };


    return (
        <Transition.Root show={open} as={Fragment}>
            <Dialog as="div" className="relative z-10" initialFocus={cancelButtonRef} onClose={setOpen}>
                <Transition.Child
                    as={Fragment}
                    enter="ease-out duration-300"
                    enterFrom="opacity-0"
                    enterTo="opacity-100"
                    leave="ease-in duration-200"
                    leaveFrom="opacity-100"
                    leaveTo="opacity-0"
                >
                    <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
                </Transition.Child>

                <div className="fixed inset-0 z-10 w-screen overflow-y-auto">
                    <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
                        <Transition.Child
                            as={Fragment}
                            enter="ease-out duration-300"
                            enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                            enterTo="opacity-100 translate-y-0 sm:scale-100"
                            leave="ease-in duration-200"
                            leaveFrom="opacity-100 translate-y-0 sm:scale-100"
                            leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                        >
                            <Dialog.Panel className="relative transform overflow-hidden rounded-3xl bg-white text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg">
                                <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4">
                                    <div className="sm:flex sm:items-start justify-center">
                                        <div className="mt-3 text-center sm:ml-4 sm:mt-0 sm:text-left">
                                            <Dialog.Title as="h3" className="text-3xl font-bold leading-6 text-gray-800">
                                                이미 링쿡에 등록된 요리네요!
                                            </Dialog.Title>
                                            <div className="flex justify-center mt-5">
                                                <div className="w-36 h-1 bg-green-500 rounded-sm" />
                                            </div>
                                            <div className="mt-5 text-center">
                                                <p className="text-xl text-gray-500">
                                                    아래 <span style={{ color: 'rgb(72, 187, 120)', fontWeight: 'bold' }}>보러가기</span>를 눌러보세요😎 <br />
                                                    가까운 마트에서 요리 재료를 판매 중인지 <br />
                                                    한 번에 확인할 수 있어요.
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <div className="bg-gray-50 px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6 justify-center">
                                    <button
                                        type="button"
                                        className="inline-flex w-full justify-center rounded-xl bg-green-500 px-20 py-5 text-lg font-semibold text-white shadow-sm hover:bg-green-400 sm:ml-3 sm:w-auto"
                                        onClick={redirectToContentDetail}
                                    >
                                        보러가기
                                    </button>
                                    <button
                                        type="button"
                                        className="mt-3 inline-flex w-full justify-center rounded-xl bg-white px-20 py-5 text-lg font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50 sm:mt-0 sm:w-auto"
                                        onClick={() => setOpen(false)}
                                        ref={cancelButtonRef}
                                    >
                                        닫기
                                    </button>
                                </div>

                            </Dialog.Panel>
                        </Transition.Child>
                    </div>
                </div>
            </Dialog>
        </Transition.Root>
    )
}
